<template>
  <div id="login">
    <Header />
    <div class="painel">
      <div class="">
        <div class="content">
          <form>
            <div class="form">
              <div class="painel-title">Ol√°,</div>
              <div class="painel-text">Fa√ßa login para continuar</div>
              <label for="inputs"> Email</label>
              <div class="submit-line">
                <input id="inputs" class="input" placeholder="Digite o e-mail cadastrado" type="text"
                  v-model="form.login" />
              </div>
              <label for="inputs"> Senha</label>
              <div class="submit-line">
                <input id="inputs" class="input" placeholder="Digite sua senha" type="password"
                  v-model="form.password" />
              </div>
              <div v-show="confirmEmail">
                <label for="inputs"> C√≥digo de confirma√ß√£o no E-mail</label>
                <div class="submit-line">
                  <input id="inputs" class="input" placeholder="Digite o c√≥digo de confirma√ß√£o" type="text"
                    v-model="form.confirmEmail" />
                </div>
              </div>
              <div v-show="require2FA">
                <label for="inputs"> C√≥digo 2FA</label>
                <div class="submit-line">
                  <input id="inputs" class="input" placeholder="Digite seu 2FA" type="text" v-model="form.code" />
                </div>
              </div>
              <p class="recovery">
                <a href="">
                  Esqueci a senha
                </a>
              </p>
              <div class="col-4">
                <div @click="login" class="btn-login">
                  Login
                </div>
              </div>
              <div class="col-4">
                <div @click='register' class="btn-cadastro">
                  Cadastrar
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from '../web/Header.vue'
import notify from '../../utils/notify'
export default {
  name: 'Login',
  components: {
    Header,
  },
  data() {
    return {
      require2FA: false,
      confirmEmail:false,
      form: {
        login: '',
        password: '',
        code: '',
        confirmEmail:''
      }
    }
  },
  methods: {
    register() {
      this.$router.push({ name: "Register" });
    },
    login() {
      if (this.require2FA) this.login2FA()
      else if (this.confirmEmail) this.loginConfirmEmail()
      else this.loginBase();
    },
    loginBase() {
  
      var data = new FormData();
      data.append("email", this.form.login);
      data.append("password", this.form.password);     
      this.axios({
        method: "POST",        
        url: this.$API_URL + "/Auth/login",
        data: data,
        headers: {
          "Content-Type": "multipart/form-data"
        }
      })
        .then(res => {
          console.log(res);
          window.open('http://localhost:8080/', '_blank')        
          
        })
        .catch(err => {
          if (err && err.response && err.response.data) {
            if (err.response.data.DATA == 'REQUIRE_2FA') {
              notify('error', 'Insira c√≥digo do google authenticator')
              this.require2FA = true;
            }
            if (err.response.data.DATA == 'CONFIRM_YOUR_EMAIL') {
              notify('error', 'Confirme seu e-mail')
              this.confirmEmail = true;
            }
          }
          
          else notify('error', "INVALID_ERROR");
        });
    },
    login2FA() {
      var data = new FormData();
      data.append("email", this.form.login);
      data.append("password", this.form.password);
      data.append('code', this.form.code);
      this.axios({
        method: "POST",
        url: this.$API_URL + "/Auth/login",
        data: data,
        headers: {
          "Content-Type": "multipart/form-data"
        }
      })
        .then(res => {
          alert('logado')
          this.require2FA = false;
          console.log(res)
        })
        .catch(err => {
          console.log(err)
          notify('error', 'Senha ou 2FA inv√°lidos')         
          
          
        });
    },
    loginConfirmEmail() {
      var data = new FormData();
      data.append("email", this.form.login);
      data.append("password", this.form.password);
      data.append('codeEmail', this.form.confirmEmail);
      this.axios({
        method: "POST",
        url: this.$API_URL + "/Auth/login",
        data: data,
        headers: {
          "Content-Type": "multipart/form-data"
        }
      })
        .then(res => {
          alert('logado');
          console.log(res)
          this.confirmEmail = false;
        })
        .catch(err => {
          console.log(err)
            notify('error', `C√≥digo de confirma√ß√£o de e-mail inv√°lido`);          
        });
    }
  }
}
</script>

