<template>
  <div id="painel">
    <Header />
    <div class="painel">
      <div class="">
        <div class="content">
          <form>
            <div class="form">
              <div class="painel-title">Criar conta</div>
              <div class="painel-text">
                Preencha o formul√°rio abaixo para criar seu cadastro.
              </div>
              <div class="account-type">
                <div class="sub-title">Tipo de conta</div>
                <div class="radio-group">
                  <div class="custom-radio">
                    <input id="radiobox-1" type="checkbox" @click="selectType('CPF')" v-model="pf" />
                    <label for="radiobox-1">Pessoa f√≠sica</label>
                  </div>
                  <div class="custom-radio">
                    <input id="radiobox-2" type="checkbox" @click="selectType('CNPJ')" v-model="pj" />
                    <label for="radiobox-2">Pessoa J√∫ridica</label>
                  </div>
                </div>      
                
              </div>
              <label for="inputs" v-if="pf == true">Nome completo</label>
              <label for="inputs" v-else>Nome da empresa</label>
              <div class="submit-line" v-if="pf == true">
                <input
                  style="width: 100%"
                  id="inputs"
                  class="input"
                  placeholder="Digite seu nome completo"
                  type="text"
                  required
                  v-model="form.name"
                />
              </div>
              <div class="submit-line" v-else>
                <input
                  style="width: 100%"
                  id="inputs"
                  class="input"
                  placeholder="Digite o nome da empresa"
                  type="text"
                  required
                  v-model="form.name"
                />
              </div>
              <div class="input-group">
                <div class="submit-line">
                  <label for="" v-if="pf">CPF</label>
                  <label for="" v-if="pj">CNPJ</label>
                  <input
                  v-if="pf"
                    id="inputs"
                    class="input"
                    placeholder="Digite seu CPF"
                    type="text"
                    v-mask="'###.###.###-##'"
                    :state="statusValidDocument" @change="isValidCpfCnpj"
                    :class="statusValidDocument == false ? 'invalid-border': ''"
                    v-model="form.document"
                  />
                  <input
                  v-if="pj"
                    id="inputs"
                    class="input"
                    placeholder="Digite seu CPNJ"
                    type="text"
                    v-mask="'##.###.###/####-##'"
                    :state="statusValidDocument" @change="isValidCpfCnpj"
                    :class="statusValidDocument == false ? 'invalid-border': ''"
                    v-model="form.document"
                  />
                  <div v-if="statusValidDocument != true && statusValidDocument != null" style="color: red;">Documento
                    invalido!
                  </div>
                </div>
                <div class="submit-line">
                  <label for="" v-if="pf == true">Data de nascimento</label>
                  <label for="" v-else>Data de constitui√ß√£o</label>
                  <input
                  v-if="pf == true"
                    id="inputs"
                    class="input"
                    placeholder="Data de nascimento"
                    type="text"
                    v-mask="'##/##/####'"
                    v-model="form.birthday"
                    :state="statusValidbirth" @change="isValidAge"
                    :class="statusValidbirth == false ? 'invalid-border': ''"
                  />
                  <input
                  v-else
                    id="inputs"
                    class="input"
                    placeholder="Data de constitui√ß√£o"
                    type="text"
                    v-mask="'##/##/####'"
                    v-model="form.birthday"
                    :state="statusValidbirth" @change="isValidAge"
                    :class="statusValidbirth == false ? 'invalid-border': ''"
                  />
                  <div v-if="statusValidbirth != true && statusValidbirth != null" style="color: red;">Pro√≠bido o
                    cadastro de
                    <br />
                    menores de 18 anos!</div>
                </div>
              </div>
              <div class="input-group">
                <div class="submit-line">
                  <label for="">Email</label>
                  <input
                    id="inputs"
                    class="input"
                    placeholder="Digite seu email"
                    type="email"
                    v-model="form.email"
                    :state="statusValidEmail" @change="isValidEmail"
                    :class="statusValidEmail == false ? 'invalid-border': ''"
                  />
                  <div v-if="statusValidEmail != true && statusValidEmail != null" style="color: red;">E-mail
                    invalido!</div>
                </div>
                <div class="submit-line">
                  <label for="">Celular</label>
                  <input
                    id="inputs"
                    class="input"
                    placeholder="Digite seu celular"
                    v-mask="'## # ####-####'"
                    type="text"
                    v-model="form.phone"
                  />
                </div>
              </div>
              <hr
                style="
                  background: #646464;
                  border: 1px solid #646464;
                  margin-bottom: 20px;
                "
              />
              <label for="inputs">Senha</label>
              <div class="submit-line">
                <input
                  style="width: 100%"
                  id="inputs"
                  class="input"
                  placeholder="Crie uma senha"
                  type="password"
                  v-model="form.password"
                />
              </div>
              <label for="inputs">Confirmar senha</label>
              <div class="submit-line">
                <input
                  style="width: 100%"
                  id="inputs"
                  class="input"
                  placeholder="Crie uma senha"
                  type="password"
                  v-model="form.confirmPassword"
                />
              </div>
              <div class="termos">
                <label for="">
                  <input type="checkbox" v-model="termos" />
                  Ao me cadastrar aceito os
                  <a class="yello-ancora" href="">termos de uso</a>
                  <br />
                  e a
                  <a class="yello-ancora" href="">pol√≠tica de privacidade</a>.
                </label>
              </div>
              <div class="col-4">
                <div @click="newUser" class="btn-cadastro">Cadastrar</div>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from "../web/Header.vue";
import { validateBr } from "js-brasil";
import notify from '../../utils/notify'
export default {
  name: "Cadastro",
  components: {
    Header,
  },
  data() {
    return {
      statusValidEmail: null,
      statusValidDocument: null,
      statusValidCep: null,
      statusValidbirth: null,
      pf: true,
      pj: false,
      termos: false,
      form: {
        type: "CPF",
        name: "",
        document: "",
        birthday: "",
        email: "",
        phone: "",
        password: "",
        confirmPassword: "",
      },
    };
  },
  methods: {
    digitsVal(document) {
      if (validateBr.cpf(document) === false) {
        if (validateBr.cnpj(document) === false) {
          return false;
        }
      } else {
        return true;
      }
    },
    isValidCpfCnpj() {
      if (this.digitsVal(this.form.document) === false) {
        this.statusValidDocument = false;
      } else {
        this.statusValidDocument = null;
      }
    },
    isValidEmail() {
      const emailPattern = /^(?=[a-zA-Z0-9@.%+-]{6,254}$)[a-zA-Z0-9.%+-]{1,64}@(?:[a-zA-Z0-9-]{1,63}\.){1,8}[a-zA-Z]{2,63}$/;
      if (emailPattern.test(this.form.email) === false) {
        this.statusValidEmail = false;
      } else {
        this.statusValidEmail = null;
      }
    },
    isValidAge() {
      console.log(this.calculateAge())
      if (this.calculateAge() < 18) {

        this.statusValidbirth = false;
      } else {
        this.statusValidbirth = null;
      }
    },
    calculateAge() {
      let data = this.form.birthday.split('-').reverse().join('/');
      var d = new Date,
        ano_atual = d.getFullYear(),
        mes_atual = d.getMonth() + 1,
        dia_atual = d.getDate(),
        split = data.split('/'),
        novadata = split[1] + "/" + split[0] + "/" + split[2],
        data_americana = new Date(novadata),
        vAno = data_americana.getFullYear(),
        vMes = data_americana.getMonth() + 1,
        vDia = data_americana.getDate(),
        ano_aniversario = +vAno,
        mes_aniversario = +vMes,
        dia_aniversario = +vDia,
        quantos_anos = ano_atual - ano_aniversario;
      if (mes_atual < mes_aniversario || mes_atual == mes_aniversario && dia_atual < dia_aniversario) {
        quantos_anos--;
      }
      return quantos_anos < 0 ? 0 : quantos_anos;
    },
    selectType(type) {      
      if (type === "CPF") {
        this.pj = false;
        this.pf = !this.pf;
      }
      if (type === "CNPJ") {
        this.pf = false;
        this.pj = !this.pj;
      }
      this.form.type = type;
    },
  
    newUser(e) {
      e.preventDefault();
      
      const passwordPattern = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[$*&@#.,/%&!=+-])[0-9a-zA-Z$*&@#.,/%&!=+-]{8,}$/;
      if (
        this.form.password === "" ||
        this.form.password === "" ||
        passwordPattern.test(this.form.password) === false
      ) {
        if (passwordPattern.test(this.form.password) === false) {
          notify('error', ' "Sua senha deve conter ao menos 8 caracteres, uma letra min√∫scula, uma letra mai√∫scula e um caractere especial"');
         
        } else {
          notify('error',"Desculpa, todos os campos s√£o obrigat√≥rios.")         
        }
      } else {
        if (this.form.password === this.form.confirmPassword) {
          if (this.termos == true) {
            this.register();
          } else {
            notify('error', 'Necess√°rio assinar os termos.')
          }
        } else {
          notify('error', 'As senhas n√£o coincidem.')
        }
      }
    },
    /* formatData() {
      let data = data.split('/');
      data = data[2] + '-' + data[1] + '-' + data[0];
      data = data.reverse().join('-')      
    }, */
    register() {
      this.requesting = true;     
      var data = new FormData();        
          data.append("email", this.form.email);
          data.append("name", this.form.name);
          data.append("tax_id", this.form.document);
          data.append("tax_type", this.form.type);
          data.append("password", this.form.password);
      data.append("birthday", this.form.birthday.split('/').reverse().join('-'));
          this.axios({
            method: "POST",
            url: this.$API_URL + "/Auth/register",
            data: data,
            headers: { "Content-Type": "multipart/form-data" }
          })
            .then(response => {
              this.requesting = false;
              notify('success', `${response.data.DATA}`);            
              this.$router.push({ name: "Login" });
            })
            .catch(err => {
              this.requesting = false;
              if (err && err.response && err.response.data)
                notify('error', `${err.response.data.DATA}`)           
                
              else notify('error',"INVALID_ERROR");
            })
        
        .catch(err => {
          console.log(err)
          this.requesting = false;
        });
    }
  }
};
</script>

